import * as yup from 'yup';

export const authSchema = yup.object({
  name: yup
    .string()
    .required(' ')
    .matches(/^[a-zA-Z--\s]+$/, '       ')
    .min(2, '   ')
    .max(50, '  50 '),
  mail: yup
    .string()
    .required(' ')
    .email('  email ')
    .matches(/^[^\s@]+@[^\s@]+\.[^\s@]+$/, '  email'),
});

export type AuthFormData = yup.InferType<typeof authSchema>;
